<template>
  <v-container fill-height fluid>
    <v-row v-if="$vuetify.breakpoint.name === 'xs'">
      <v-col cols="12" class="text-center">
        <v-icon x-large color="error" class="ma-2">mdi-cancel</v-icon>

        <v-spacer></v-spacer>

        <span>
               .
        </span>
      </v-col>
    </v-row>

    <v-row v-else>
      <v-col>
        <v-sheet height="75px">
          <v-toolbar flat>
            <v-btn
              outlined
              class="mr-4"
              color="grey darken-2"
              @click="setToday"
            >
                
            </v-btn>

            <v-btn fab text small color="grey darken-2" @click="prev">
              <v-icon>
                mdi-chevron-left
              </v-icon>
            </v-btn>

            <v-btn fab text small color="grey darken-2" @click="next">
              <v-icon>
                mdi-chevron-right
              </v-icon>
            </v-btn>

            <v-toolbar-title v-if="$refs.calendar">
              {{ $refs.calendar.title }}
            </v-toolbar-title>
          </v-toolbar>
        </v-sheet>

        <v-sheet height="75vh">
          <v-calendar
            ref="calendar"
            color="accent"
            v-model="focus"
            :events="schedule == null ? [] : schedule"
            :event-color="getEventColor"
            :type="type"
            @click:event="showEvent"
          ></v-calendar>

          <v-menu
            v-model="selectedOpen"
            :close-on-content-click="false"
            :activator="selectedElement"
            transition="slide-y-reverse-transition"
            top
          >
            <v-card min-width="350px">
              <v-toolbar :color="selectedEvent.color" flat dark dense>
                <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
              </v-toolbar>

              <v-card-text>
                <span v-html="selectedEvent.details"></span>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="success darken-2"
                  @click="
                    $router.push({
                      name: 'AssignmentSubmit',
                      params: {
                        classId: selectedEvent.subjectId,
                        assignmentId: selectedEvent.assignmentId
                      }
                    })
                  "
                  :disabled="
                    selectedEvent.isSubmitted == 1 || selectedEvent.isExpired
                  "
                >
                  {{
                    selectedEvent.isSubmitted
                      ? ""
                      : selectedEvent.isExpired
                      ? ""
                      : ""
                  }}
                </v-btn>
                <v-btn text color="primary" @click="selectedOpen = false">
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-menu>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  // beforeCreate() {
  //   this.$store.dispatch("schedule/retrieveSchedule");
  // },

  computed: {
    /*
     *    name, start, end, color ,
     *  ,   YYYY-MM-DD hh:mm .
     */
    schedule() {
      return this.$store.getters["schedule/schedule"];
    },
    isLoading() {
      return this.$store.getters["schedule/isLoading"];
    }
  },

  mounted() {
    if (this.$vuetify.breakpoint.name !== "xs")
      this.$refs.calendar.checkChange();
  },

  data: () => ({
    focus: "",
    type: "month",

    typeToLabel: {
      month: "Month",
      week: "Week"
    },

    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,

    colors: [],
    names: []
  }),

  methods: {
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },

    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },

    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => {
          this.selectedOpen = true;
        }, 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    }
  }
};
</script>
<style></style>
